.App {
  font-family: sans-serif;
  text-align: center;
}

.title {
  text-align: center;
}

.weather {
  font-size: 2em;
  color: black;
  font-family: sans-serif;
  font-weight: 600;
  text-shadow: 3px 3px 6px gray;
  cursor: pointer;
  text-align: center;
}

.center {
  text-align: center;
  list-style-type: none;
  margin: auto;
}

body {
  background: rgba(0, 0, 0, 0);
}

.italicize {
  font-style: italic;
  font-size: 0.8em;
}

.boldTitle {
  font-weight: bold;
  font-size: 1.1em;
}

.loading {
  text-align: center;
}

.loader {
  border: 11px solid #f3f3f3;
  /* Light grey */
  border-top: 11px solid #3498db;
  /* Blue */
  border-radius: 50%;
  width: 30px;
  height: 30px;
  -webkit-animation: spin 0.7s linear infinite;
          animation: spin 0.7s linear infinite;
  margin: auto;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

